/*------------------------------*\
    #light-theme
\*------------------------------*/

.light-theme {
  --list-bg: hsl(0, 0%, 98%);
  --main-bg: hsl(236, 33%, 92%);
  --text-gray: hsl(233, 11%, 84%);
  --text-hover: hsl(240, 4%, 5%);
  --text-completed-primary: hsl(236, 9%, 61%);
  --text-completed-seconday: hsl(237, 14%, 26%);
  --text-color: hsl(235, 19%, 35%);
  --header-bg-image-mobile: url("../assets/images/bg-mobile-light.jpg");
  --header-bg-image-desktop: url("../assets/images/bg-desktop-light.jpg");
  --theme-icon: url("../assets/images/icon-moon.svg");
}
